<template>
  <div id="app">
    <img src="./assets/logo.png" />
    <div>
      <h1 class="description">点击按钮上传 →</h1>
      <duo-image-uploader
        :limit="limit"
        :maxSize="maxSize"
        :imageSrc="imageSrc"
        @errMessage="errMessage"
        :acceptType="acceptType"
        @uploadChange="uploadChange"
      />
    </div>
  </div>
</template>

<script>
export default {
  name: "App",
  data() {
    return {
      limit: 3,
      imageSrc: [],
      maxSize: 1024,
      acceptType: ["png", "jpeg", "jpg"],
    };
  },
  methods: {
    uploadChange(item, list) {
      console.log(item, list);
    },
    errMessage(message) {
      console.log(message);
      alert(message);
    },
  },
};
</script>

<style>
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
.description {
  display: inline-block;
  vertical-align: 50px;
  margin-right: 20px;
}
.btn {
  vertical-align: 3px;
}
</style>



